*{
  padding:0;
  margin:0;
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
 
}

.navbar{
  width: 100%;
  height:10vmin;
  background:#233240;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  place-items:center;
  position:relative;

}

.navbar >a {
  color:white;
  font-size:2vmin;
  margin-right:5vmin;
  text-decoration:none;
  font-weight:bold;
  transition:all 300ms linear;
}

.navbar  > a:hover{
  color:rgb(248, 255, 254);
  background:#15967d;
  padding:2vmin;
  border-radius:1vmin;
}


.head{
  color:#e8e6e3;
  font-size:3vmin;
  position: absolute;
  left:5%;
  font-weight: bold;;
}

.pageHead{
  width: 100%;height:70vmin;
  background:#15967d;
  position:relative;
}

.pageHead > img{
  width:15rem;height: 15rem;
  border-radius:50%;
  position: absolute;
  left:44%;
  top:14%;
  filter:drop-shadow(4px 4px 4px black);
}

.heading{
  width:100%;
  height:20%;
  
  position: absolute;
  top:50%;
  display:grid;
  place-items:center;
  font-size: 7vmin;
  font-weight: bold;
  color:#e8e6e3
}


.starLine{
  width: 100%;
  height:10vmin;
  
  display: grid;
  font-size: 4vmin;
  color:white;
  place-items: center;
  position:absolute;
  bottom:20%;
}


.starLine::before{
  content:"";
  width: 25vmin;
  background:rgb(37, 36, 36);
  height:.5vmin;
  position:absolute;
  transform: translateX(-60%);
}
.starLine::after{
  content:"";
  width: 25vmin;
  background:rgb(39, 38, 38);
  height:.5vmin;
  position:absolute;
  transform: translateX(60%);
}

.intro{
  width: 100%;
  height:10vmin;
  
  position: absolute;
  bottom:5%;
  font-size:3vmin;
  text-align: center;
  color:white;
}


.portfolio{
  width: 100vw;
  height:90vh;
 overflow: hidden;
 display: grid;
 place-items: center;
 position: relative;
}

.containCard{
  width: 80%;
  height:70%;
  
  position: absolute;
  display: flex;
  flex-direction:row;
  flex-wrap: wrap;
  justify-content: space-around;
  bottom:14%;
}
.headPortfolio{
position: absolute;
font-size:5vmin;
color:black;
top:0%;
font-weight: bold;
}

.card{
  width:40vmin;
  height:30vmin;
  position: relative;
  border-radius:1vmin;
  overflow: hidden;
}

.card > img{
  width:100%;
  height:100%;
  border-radius:1vmin;
  position:absolute;
}

.starLine1{
  width: 100%;
  height:10vmin;
  
  display: grid;
  font-size: 4vmin;
  color:rgb(0, 0, 0);
  place-items: center;
  position:absolute;
  top:5%;
}


.starLine1::before{
  content:"";
  width: 15vmin;
  background:rgb(37, 36, 36);
  height:.5vmin;
  position:absolute;
  transform: translateX(-65%);
}
.starLine1::after{
  content:"";
  width: 15vmin;
  background:rgb(39, 38, 38);
  height:.5vmin;
  position:absolute;
  transform: translateX(65%);
}

.card >p{
  position:absolute;
  width:100%;
  height:100%;
  background:rgba(12, 12, 12, 0.774);
  -webkit-backdrop-filter: blur(15px);
  backdrop-filter: blur(15px);
  color:white;
  padding:3vmin;
  font-size:2vmin;
  opacity: 0;
  transition:all 350ms ease-in-out;
  transform:translateY(100%)
}



.card:hover > p{
  transform:translate(0%);
  opacity: 3;
  text-shadow: 0px 0px 1px white;
}

.about{
  width:100vw;
  height:100vh;
  border:1px solid black;
  background:#1abc9c;
  position:relative;
  overflow: hidden;
}
.aboutLine{
  font-size:8vmin;
  width:100%;
  height: 10vmin;
  text-align:center;
  font-weight:bold;
  color:white;
  position: absolute;
  top:10%;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.starLine2{
  width: 100%;
  height:10vmin;
  
  display: grid;
  font-size: 4vmin;
  color:rgb(0, 0, 0);
  place-items: center;
  position:absolute;
  top:60%;
}


.starLine2::before{
  content:"";
  width: 15vmin;
  background:rgb(83, 78, 78);
  height:.5vmin;
  position:absolute;
  transform: translateX(-65%);
}
.starLine2::after{
  content:"";
  width: 15vmin;
  background:rgb(95, 89, 89);
  height:.5vmin;
  position:absolute;
  transform: translateX(65%);
}

.firstPara,.secondPara{
  width: 40%;
  height:50vmin;
  padding:2vmin;

  position: absolute;
  font-size:3.5vmin;
  top:18vmin;
  text-align: left;
}

.firstPara{
  left:10%;
}
.secondPara{
  right:10%;
}


.download{
  width:25vmin;
  height:8vmin;
  border:1px solid white;
  border-radius:1vmin;
  font-size:3vmin;
   background:transparent;
   position: absolute;
   top:750%;
   left:43%;
}

.download:hover{
  background:white;
}



.contact{
width:100vw;
height:80vh;
position: relative;

  
}

.starLine3{
  width: 100%;
  height:10vmin;
  
  display: grid;
  font-size: 4vmin;
  color:rgb(251, 251, 251);
  place-items: center;
  position:absolute;
  top:4%;
}


.starLine3::before{
  content:"";
  width: 15vmin;
  background:rgb(170, 168, 168);
  height:.5vmin;
  position:absolute;
  transform: translateX(-65%);
}
.starLine3::after{
  content:"";
  width: 15vmin;
  background:rgb(179, 179, 179);
  height:.5vmin;
  position:absolute;
  transform: translateX(65%);
}


.contactHead{
  font-size:4vmin;
  width:100%;
  height:10vmin;
  background:#2c3e50;
  color:white;
  top:0%;
  display: grid;
  place-items: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: bold;
}


.form{
  width:60%;
  height:70%;

  left:20%;
  position:absolute;
  top:25%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding:3vmin;
}

.form > input{
  width:100%;
  height: 15%;
  border:none;
  border-bottom:.3vmin solid black;
  font-size: 2vmin;
  font-weight: bold;
  padding:2vmin;;
}

#submit{
  width:15vmin;
  height:5vmin;
  background:#69d3be;
  color:white;
  border:none;
  outline:none;
  display: grid;
  place-items:center;
  border-radius:2vmin;
}


.footer{
  width: 100vw;
  height:50vh;
  background:#2c3e50;
  color:white;
  position: relative;
  overflow: hidden;
}
.containInfo{
  width: 100%;
  height:80%;

  top:0%;
  position:absolute;;
}

.links{
  width:25%;
  height:25%;
  transform: translate(150%,240%);
  display:flex;
  flex-direction:row;
  justify-content: space-around;
  place-items:center;
}

.links > i{
  font-size:4vmin;
  border-radius:50%;
  width:7vmin;
  height:7vmin;
  border:5px solid white;
  display: grid;
  place-items:center;;
}


.links > i:hover{
  background:white;
  color:black;
}

.foot{
  width: 100%;
  height:10%;
  background:black;
  bottom:0%;
  font-size:3vmin;
  display:grid;
  place-items:center;;
  position: absolute;
}


.navbar,.pageHead,.portfolio,.about,.contact,.footer{
  overflow: hidden;
}
@media only screen and (max-width:550px){
  .pageHead > img{
    width:25vmin;
    height:25vmin;
    left:40%;
  }

  .contact > input{
    font-size:5vmin;
  }

  .links {
    width: 55vmin;
    transform: translate(51%,240%);
  }
  .links > i{
    width:10vmin;
    height:10vmin;;
    border:2px solid white;
  }
}